<template>
  <div class="language">
    <p class="title flower-font">Languages</p>
    <two-boxes :items="firstPair"></two-boxes>
    <two-boxes :items="secondPair"></two-boxes>
  </div>
</template>

<script>
    import TwoBoxes from '../components/TwoBoxes'

    export default {
        name: 'Language',
        components: {TwoBoxes},
        component: {
            TwoBoxes
        },
        data() {
            return {
                languages: require('./languages.json')
            }
        },
        computed: {
            firstPair() {
                return [this.languages[0], this.languages[1]]
            },
            secondPair() {
                return [this.languages[2], this.languages[3]]
            }

        }
    }
</script>

